<template>
  <div class="login">
    <h1>米蕉账号登录</h1>
    <van-image width="100" height="100" :src="require('@/assets/橘子.png')" />
    <div class="title">让每个人都能享受科技的乐趣</div>
    <div class="username flex">
      <div class="tag">账号</div>
      <div>
        <input type="text" v-model="username" placeholder="请输入账号" />
      </div>
    </div>
    <div class="password flex">
      <div class="tag">密码</div>
      <div>
        <input type="password" v-model="password" placeholder="请输入密码" />
      </div>
    </div>
    <div class="register">
      <span>忘记密码？</span
      ><span
        @click="
          $router.push({
            path: '/register',
            query: {
              target: $route.query.target,
            },
          })
        "
        >立即注册</span
      >
    </div>

    <div
      style="
        width: 80%;
        margin: auto;
        display: flex;
        align-items: center;
        margin-top: 20px;
      "
    >
      <input
        type="radio"
        class="radio"
        :value="!checked"
        v-model="checked"
        name=""
        id=""
      />
      <span class="title xieyi"
        >已阅读并同意<a href="">用户协议</a>和<a href="">隐私政策</a>
      </span>
    </div>
    <div class="btn" @click="login">登录</div>
  </div>
</template>

<script>
import { Toast } from "vant";
import { mapMutations } from "vuex";
export default {
  data() {
    return {
      username: "",
      password: "",
      users: [],
      checked: false,
    };
  },
  methods: {
    ...mapMutations(["userLogin"]),
    login() {
      let registered = this.users.find((item) => {
        return (
          item.username === this.username.trim() &&
          item.password === this.password.trim()
        );
      });
      if (!this.checked) {
        Toast("请勾选协议");
        return;
      }
      if (registered) {
        console.log("登陆成功");
        this.userLogin(this.username);
        this.$router.replace(this.$route.query.target);
        this.username = "";
        this.password = "";
      } else {
        Toast("账号密码错误");
        this.password = "";
      }
      // this.users = [...this.users,{username:this.username.trim(),password:this.password.trim()}]
      // console.log(this.users);
      // localStorage.setItem('users',JSON.stringify(this.users))
    },
  },
  activated(){
    this.users= JSON.parse(localStorage.getItem("users")) || []
  }
};
</script>

<style lang="less" scoped>
.login {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  text-align: center;
  background: url("@/assets/mine.jpg");
  background-size: cover;
  z-index: 1;
  position: relative;
  color: #fff;
  ::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(10px);
    z-index: -1;
  }
  h1 {
    color: var(--text-secondary-color);
    font-size: 20px;
    font-weight: 500;
    line-height: 27px;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 0;
    padding: 0.8rem 0;
  }
  .title {
    color: #737373;
    margin: 14px auto 0;
    padding: 0 28px;
  }
  .flex {
    display: flex;
    align-items: center;
    border: 2px solid transparent;
    color: hsla(0, 0%, 100%, 0.95);
    margin: auto;
    width: 70vw;
    height: 1rem;
    background: #262626;
    border-radius: 16px;
    margin-top: 0.7rem;
    padding: 0 14px;

    .tag {
      padding: 0 0.2rem;
    }

    input {
      background: #262626;
      border: 0px;
      height: 50%;
      &::placeholder {
        color: #757575;
      }
    }
  }
  .btn {
    width: 70vw;
    margin: 20px auto;
    background: rgba(39, 122, 247, 0.3);
    border-radius: 14px;
    padding: 12px;
    margin-top: 0.7rem;
  }
  .register {
    color: #737373;
    font-size: 0.25rem;
    display: flex;
    justify-content: space-around;
    margin-top: 0.2rem;
  }
  .xieyi {
    padding: 0;
    margin: 0;
    display: inline-block;
  }
  .radio {
    display: block;
    width: 20px;
  }
}
</style>